body {
  font-family: getFont("Montserrat");
  font-size: 14px;
  font-weight: 400;
  line-height: 1.214;
}

body.dark {
  background-color: #6b6b6b;
}

body.dark .legend__info, body.dark .show {
  background-color: #4c4e51;
}

body.dark .show__svg {
  fill: #fff;
}

.menu {
  background-color: #525252bd;
}

.main {
  gap: 27px;
  display: flex;
}

.header {
  justify-content: flex-end;
  align-items: center;
  padding: 49px 35px;
  display: flex;
}

.theme-toggle {
  cursor: pointer;
  background-color: #b2c6d8;
  border-radius: 50px;
  width: 90px;
  height: 38px;
  padding: 5px 7px;
  transition: background-color .25s ease-in-out;
}

.theme-toggle.dark {
  background-color: #fff;
}

.theme-toggle.dark .theme-toggle__circle-icon--dark {
  display: block;
}

.theme-toggle.dark .theme-toggle__circle-icon--light {
  display: none;
}

.theme-toggle.dark .theme-toggle__circle {
  background-color: #b2c6d8;
  transform: translateX(0);
}

.theme-toggle__circle {
  background-color: #fff;
  border-radius: 100%;
  width: 28px;
  height: 28px;
  padding: 4px;
  transition: transform .25s ease-in-out, background-color .25s ease-in-out;
  transform: translateX(48px);
}

.theme-toggle__circle-icon {
  fill: #000;
  width: 20px;
  height: 20px;
}

.theme-toggle__circle-icon--dark {
  display: none;
}

.menu {
  background: #00417d4d;
  border-radius: 49px;
  max-width: 320px;
  padding: 20px 15px;
}

.menu-filter {
  background: #fff;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  gap: 7px;
  padding: 8px;
  display: flex;
}

.menu-filter__list {
  color: #000;
  cursor: pointer;
  background: #fff;
  border: 1px solid #686868;
  border-radius: 10px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  padding: 3.4px;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: all .9s;
  display: flex;
}

.menu-filter__list.active {
  color: #fff;
  background: #04aa6d;
}

.menu-cards {
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px;
  margin-top: 10px;
  display: grid;
}

.menu-cards-card {
  background: #fff;
  border-radius: 18px;
  padding: 10px;
  box-shadow: -4px 6px 14.7px 1px #00000040;
}

.menu-cards-card__img {
  aspect-ratio: 147 / 128;
  flex-shrink: 0;
  width: 117px;
}

.menu-cards-card__title {
  color: #000;
  text-align: left;
  margin-top: 10px;
  font-family: Noto Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.menu-cards-card__category {
  color: #5b5b5b;
  text-align: left;
  font-family: Noto Sans, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.furniture__spawn {
  width: 100%;
}

.menu-cards-card {
  transition: transform .3s;
}

.menu-cards-card.active {
  z-index: 1;
  transform: scale(1.05);
}

@media (width >= 768px) {
  .menu {
    max-width: 413px;
  }

  .menu-filter {
    column-gap: 11px;
  }

  .menu-filter__list {
    padding: 7px 16px;
  }

  .menu-cards-card {
    padding: 16px;
  }

  .menu-cards-card__title {
    font-size: 18px;
  }

  .menu-cards-card__img {
    width: 152px;
  }
}

@media (width >= 1024px) {
  .menu {
    min-width: 401px;
  }

  .menu-filter {
    column-gap: 8px;
  }

  .menu-filter__list {
    padding: 7px 12px;
    font-size: 14px;
  }

  .menu-cards-card {
    padding: 15px;
  }

  .menu-cards-card__title {
    font-size: 16px;
  }

  .menu-cards-card__img {
    width: 147px;
  }

  .menu-cards-card__category {
    font-size: 12px;
  }
}

.legend__wrapper {
  max-width: 302px;
}

.legend__info {
  color: #fff;
  background-color: #b2c6d8;
  border-radius: 20px;
  flex-direction: column;
  align-items: flex-start;
  gap: 15px;
  padding: 15px;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.legend__item {
  align-items: center;
  gap: 10px;
  display: flex;
}

.legend__text--button {
  border: 2px solid #fff;
  border-radius: 10px;
  padding: 10px 20px;
  display: inline-block;
}

.show, .close-legend {
  display: none;
}

@media (width >= 768px) {
  .legend__container {
    border-radius: 20px;
    display: block;
    overflow: hidden;
  }

  .legend__wrapper {
    max-width: 385px;
    display: block;
    position: relative;
  }

  .legend__info {
    opacity: 0;
    background-color: #b2c6d8;
    border-radius: 20px;
    padding: 20px 15px 30px 25px;
    font-size: 12px;
    transition: opacity .5s cubic-bezier(.4, 0, .2, 1), transform .5s cubic-bezier(.4, 0, .2, 1);
    display: none;
    transform: translateY(-50px);
  }

  .legend__list {
    font-size: 14px;
  }

  .show {
    background-color: #b2c6d8;
    border-radius: 20px;
    justify-content: center;
    width: 100%;
    padding: 27px 0;
    transition: opacity .3s linear;
    display: flex;
  }

  .show__svg {
    fill: #4a4a4a;
    width: 54px;
    height: 35px;
    display: block;
    transform: rotate(180deg);
  }

  .close-legend {
    opacity: 0;
    z-index: 10;
    background-color: #fff;
    border-radius: 29px;
    max-width: 59px;
    padding: 3px 16px;
    transition: opacity .5s;
    display: none;
    position: absolute;
    bottom: -17px;
    right: -10px;
  }

  .close-legend__svg {
    fill: #4a4a4a;
    width: 27px;
    height: 28px;
    display: block;
  }
}

@media (width >= 1440px) {
  .legend__info {
    gap: 22px;
    padding: 35px 35px 35px 30px;
    font-size: 18px;
    transform: translateY(-50px);
  }

  .legend__wrapper {
    max-width: 423px;
  }

  .info-button {
    padding: 10px 15px;
  }

  .show {
    width: 100%;
    padding: 35px 0;
  }

  .show__svg {
    width: 54px;
    height: 35px;
  }
}
